import React, { Component } from 'react'
import Article from 'Article';
import CategoryList from 'Article/CategoryList';

import articleRepository from 'libs/ArticleRepository';
import articleJson from 'data/articles.json';

export class BlogPost extends Component {
  constructor(props) {
    super(props);
    const articleSlug = this.props.match.params.slug;
    const article = articleJson.find(article => { return article.slug === articleSlug })

    this.state = {
      title: article.title,
      body: article.body,
      publishedAt: article.date,
      categories: article.categories,
      categoriesWithCount: []
    };
  }

  componentDidMount() {
    articleRepository.categoriesWithCount((categoriesWithCount) => {
      this.setState({ categoriesWithCount: categoriesWithCount })
    })
  }

  categoriesComponent() {
    if (Array.isArray(this.state.categoriesWithCount) && this.state.categoriesWithCount.length > 0) {
      return (<CategoryList categories={this.state.categoriesWithCount} />)
    }
  }

  render() {
    return (
      <Article
        title={this.state.title}
        publishedAt={this.state.publishedAt.format("DD MMM YY")}
        body={this.state.body}
        categoryList={this.categoriesComponent()}
      />
    )
  }
}

export default BlogPost
